        }
    },
    methods: {
        funHeader(eventName) {
            if (eventName === 'sidebar') {
                this.sidebar.open = !this.sidebar.open
            } else if (eventName === 'to-dashboard') {
                this.$router.push('/dashboard')
            } else if (eventName === 'to-login') {
                this.$router.push('/auth/login')
            }
        },
    },
    components: {
        HomeHeader,
        NavBars,
    }
}
